import React, {useState} from 'react'

export default function RegisterForm(){


	const [state, setState] = useState({
		form: {} 
	})


	function handleClick(e){
		if(!state.form['email'] || !state.form['password']){
			alert('Llena todos los campos por favor')
		}else{
			fetch('http://localhost:8000/api/users', {
				body: JSON.stringify({
					email: state.form['email'],
					password: state.form['password']
				}),
				method: 'POST',
				headers:{
   				 'Content-Type': 'application/json'
  				}
			}).then(res=>{

				return res.json();

			}).then((data)=>{
				alert('Correct Registration')	
			}).catch((err)=>{
				alert('Error al registrarse')
			})
		}
	}

	function handleChange(e){
		setState({
			form: {
				...state.form,
				[e.target.name]: e.target.value 
			} 
		})
	}

	return(
		<form className="w-50 mx-auto mt-5 border border-secondary p-4 rounded">
		  <div className="form-group row">
		    <label for="inputEmail3" className="col-12 col-form-label">Email</label>
		    <div className="col-12">
		      <input type="email" name="email" onChange={handleChange} className="form-control" id="inputEmail3" placeholder="Email"/>
		    </div>
		  </div>

		  <div className="form-group row">
		    <label for="inputPassword3" className="col-12 col-form-label">Password</label>
		    <div className="col-12">
		      <input type="password" name="password" onChange={handleChange} className="form-control" id="inputPassword3" placeholder="Password"/>
		    </div>
		  </div>

		  <fieldset className="form-group mt-4">
		    <div className="row">
		      <legend className="col-form-label col-12 pt-0">Soy</legend>
		      <div className="col-12">
		        <div className="form-check">
		          <input className="form-check-input" type="radio" name="gridRadios" id="gridRadios1" value="option1" />
		          <label className="form-check-label" for="gridRadios1">
		           	Empresa 
		          </label>
		        </div>
		        <div className="form-check">
		          <input className="form-check-input" checked type="radio" name="gridRadios" id="gridRadios2" value="option2"/>
		          <label className="form-check-label" for="gridRadios2">
		           	Persona Fisica 
		          </label>
		        </div>
		      </div>
		    </div>
		  </fieldset>

		  <div className="form-group mt-4 row">
		    <div className="col-12">Acepto los terminos y condiciones</div>
		    <div className="col-12">
		      <div className="form-check">
		        <input className="form-check-input" checked type="checkbox" id="gridCheck1"/>
		        <label className="form-check-label" for="gridCheck1">
		          Acepto
		        </label>
		      </div>
		    </div>
		  </div>

		  <div className="form-group row mt-3">
		    <div className="col-12">
		      <button type="button" onClick={handleClick} className="w-100 btn btn-primary">Sign in</button>
		    </div>
		  </div>
		</form>	
	)

}
